import Input from './common/input';
import { toast } from 'react-toastify';
import { useState } from 'react';
import { useEffect } from 'react';
import { useFormik } from 'formik';
import { useHistory } from 'react-router-dom';
import { htmlEntities } from '../services/generalFn';
import { getCurrentUser } from '../services/usersService';
import { addInquiry } from '../services/inquiriesService';

const Contact = () => {

    const[isLogin, setIsLogin] = useState();

    useEffect(() => setIsLogin(getCurrentUser()), []);

    const styles = {
        textarea: {whiteSpace: 'pre-wrap', height: '100px'},
    }

    const[inputsFocus ,setInputsFocus] = useState({});

    const history = useHistory();

    const validate = values => {
        const errors = {};

        if(!values.name) errors.name = ' * ';
        else if(values.name.length < 2) errors.name = ' *      ';
        else if(values.name.length > 100) errors.name = ' *       ';

        if(!values.email) errors.email = ' * ';
        else if(!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) errors.email = ' *  ';

        if(!values.msg) errors.msg = ' * ';
        else if(values.msg.length < 16) errors.msg = ' *       ';
        else if(values.msg.length > 500) errors.msg = ' *        ';

        return errors;
    }

    const onSubmit = async values => {
        try {
            values.user_id = isLogin.id;
            await addInquiry(values);
            toast(`${values.name}     :)`);
            history.replace('home');
        } catch(err) { console.dir(err); toast.error('     ') }
    }

    const formik = useFormik({
        initialValues: {
            name: '',
            email: '',
            msg: ''
        },
        validate,
        onSubmit
    });

    return(
        <div className="container py-5">
        {isLogin ? <>
            <h1 className="text-center text-info pt-5">  </h1>
            <h6 className="text-center text-primary">      :)</h6>
            <form className="form contact-form pb-5 pt-3 mx-md-5 px-xl-5" onSubmit={formik.handleSubmit}>
                <label className="pr-1 mt-2 text-right d-block" htmlFor="name"> </label>
                <Input
                    inputData={{type: 'text', field: 'name', placeholder: ' ', inputsFocus}}
                    inputFn={{formik, setInputsFocus}}
                />
                <label className="pr-1 mt-2 text-right d-block" htmlFor="email"> </label>
                <Input
                    inputData={{type: 'email', field: 'email', placeholder: ' ', inputsFocus}}
                    inputFn={{formik, setInputsFocus}}
                />
                <label className="pr-1 mt-2 text-right d-block" htmlFor="msg"> </label>
                <textarea 
                    type="text" className="form-control" name="msg" id="msg" placeholder=" "
                    style={styles.textarea}
                    value={htmlEntities(formik.values.msg)}
                    onChange={formik.handleChange}
                    onFocus={() => setInputsFocus(inputs => ({...inputs, msg: true}))}
                    onBlur={(e) => {
                        formik.handleBlur(e);
                        setInputsFocus(inputs => ({...inputs, msg: false}));
                    }}
                ></textarea>
                {formik.touched.msg && formik.errors.msg? 
                    <small className="text-danger text-right d-block">{formik.errors.msg}</small>
                :null}
                <button type="submit" className="btn btn-dark btn-block my-5"></button>
            </form>
        </>:  
            <div className="please-login mt-5">
                <h3 className="text-center text-info">     </h3>
                <h6 className="text-center text-primary">       </h6>
            </div>
        }
        </div>
       
    );
}

export default Contact;