import Input from './common/input';
import { toast } from 'react-toastify';
import { connect } from "react-redux";
import { useState } from 'react';
import { useFormik } from 'formik';
import { useHistory } from 'react-router-dom';
import { userSignUp } from '../services/usersService';
import { createCart } from '../services/cartService';
import { indexesManager, LOGIN_INDEX, SIGNUP_DATA } from '../redux-store/indexes-reduser';

const SignUp = ({indexesManager}) => {

    const[inputsFocus ,setInputsFocus] = useState({});

    const history = useHistory();

    const validate = values => {
        const errors = {};

        if(!values.name) errors.name = ' * ';
        else if(values.name.length < 2) errors.name = ' *      ';
        else if(values.name.length > 100) errors.name = ' *       ';

        if(!values.email) errors.email = ' * ';
        else if(!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) errors.email = ' *  ';

        if(!values.password) errors.password = ' * ';
        else if(values.password.length < 6) errors.password = ' *      ';
        else if(values.password.length > 100) errors.password = ' *       ';

        if(!values.verifyPass) errors.verifyPass = ' * ';
        else if(values.verifyPass !== values.password) errors.verifyPass = ' *    ';
        
        return errors;
    }

    const onSubmit = async values => {
        let user = { name: values.name, email: values.email, pass: values.password };
        try {
            let userInfo = await userSignUp(user);
            await createCart(userInfo._id);

            toast(`${user.name}    `);
            history.replace('home');
            
            indexesManager(SIGNUP_DATA, user);
            indexesManager(LOGIN_INDEX, true);
        } catch(err) { toast.error('     ') }
    }

    const formik = useFormik({
        initialValues: {
            name: '',
            email: '',
            password: '',
            verifyPass: ''
        },
        validate,
        onSubmit
    });

    return(
        <div className="container py-5">
            <h1 className="text-center text-info pt-5"> </h1>
            <h6 className="text-center text-primary">       :)</h6>
            <form className="form sign-up-form pb-5 pt-3 mx-md-5 px-xl-5" onSubmit={formik.handleSubmit}>
                <label className="pr-1 mt-2 text-right d-block" htmlFor="name"> </label>
                <Input
                    inputData={{type: 'text', field: 'name', placeholder: ' ', inputsFocus}}
                    inputFn={{formik, setInputsFocus}}
                />
                <label className="pr-1 mt-2 text-right d-block" htmlFor="email"> </label>
                <Input
                    inputData={{type: 'email', field: 'email', placeholder: ' ', inputsFocus}}
                    inputFn={{formik, setInputsFocus}}
                />
                <label className="pr-1 mt-2 text-right d-block" htmlFor="password"></label>
                <Input
                    inputData={{type: 'password', field: 'password', placeholder: '', inputsFocus}}
                    inputFn={{formik, setInputsFocus}}
                />
                <label className="pr-1 mt-2 text-right d-block" htmlFor="verifyPass"> </label>
                <Input
                    inputData={{type: 'password', field: 'verifyPass', placeholder: ' ', inputsFocus}}
                    inputFn={{formik, setInputsFocus}}
                />
                <button type="submit" className="btn btn-dark btn-block my-5"></button>
            </form>
        </div>
    );
}

const mapStateToProps = ({indexes}) => ({indexes});
const mapDispatchToProps = (dispatch) => ({indexesManager: (type, state) => dispatch(indexesManager(type, state))})
  
export default connect(mapStateToProps, mapDispatchToProps)(SignUp);